html {
  box-sizing: border-box;
}

*, :after, :before {
  box-sizing: inherit;
}

body {
  background-color: #bf1650;
  font-family: Lato, sans-serif;
}

.container {
  max-width: 600px;
  flex-direction: column;
  justify-content: center;
  margin: 30px auto 150px;
  display: flex;
}

.main-container {
  background-color: #081229;
  border-radius: 6px;
  margin: 10px;
  padding: 50px;
  box-shadow: 0 18px 50px -10px #0003;
}

.header {
  padding: 20px;
}

.header__heading {
  color: #fff;
  text-align: center;
  letter-spacing: 1px;
  word-spacing: 3px;
  font-size: 40px;
}

.list {
  color: #fff;
  padding: 20px;
  font-size: 18px;
  line-height: 2;
  list-style: none;
}

.list__title {
  color: #2ddd10;
}

.list__text {
  color: #cd7b21;
}

.list__tutor {
  border-bottom: 2px solid #bf1650;
  display: inline-block;
}

.list__tutor:first-letter {
